body{
  background-color:#22254b
}
.app-container{
  display:flex;
  flex-wrap:wrap;
  justify-content:center;
}
.movie-container{
  width:250px;
  margin:16px;
  background-color:#373b69;
  color:#fff;
  border-radius:5px;
  box-shadow:3px 3px 5px rgba(0,0,0,0.1)
}
.movie-container p{
  margin:0
}
.movie-container img{
  max-width:100%;
  height:375px;
}
.movie-info{
  display:flex;
  padding:20px;
  justify-content:space-between;
  align-items:center;
}
.movie-info h4{
  margin:0;
  word-break:keep-all
}
.movie-info span{
  margin-left:3px
}